<template>
    <div class="body d-flex justify-content-center align-items-center">
        <ConfigComponent v-if="!isConf" @data="receiveConf"/>
        <timerComponent v-if="isConf" :pause="timer.Pause" :working="timer.Work"/>
    </div>
</template>

<script setup>
    import ConfigComponent from "./components/config_timer.vue"
    import timerComponent from "./components/timer_component.vue"
    import { ref } from 'vue';

    const isConf = ref(false);
    const timer = ref({
        'Pause': 0,
        'Work': 0
    });

    const receiveConf = (data) => {
        isConf.value = true;
        timer.value.Pause = data.pause;
        timer.value.Work = data.working;
        console.log(timer.value);
    }
</script>

