<template>
    <div v-if="value_01" @click="click_01" :class="['d-flex', 'justify-content-center', 'align-items-center', 'flex-column', 'circle', { 'active': true }]">
        <h1>Configuration</h1>
        <p>Click to start</p>
    </div>

    <div v-if="!value_01 && value_02" :class="['d-flex', 'justify-content-center', 'align-items-center', 'flex-column', 'circle', { 'active': false }]">
        <h3>Working time</h3>
        <div class="mt-2 input-group flex-nowrap justify-content-center">
            <input v-model="working" type="number" class="input-group-number rounded-input" placeholder="Minutes">
            <button v-if="working" @click="click_02" class="btn btn-outline-secondary">Next</button>
        </div>
    </div>

    <div v-if="!value_02" :class="['d-flex', 'justify-content-center', 'align-items-center', 'flex-column', 'circle', { 'active': false }]">
        <h3>Pause time</h3>
        <div class="mt-2 input-group flex-nowrap justify-content-center">
            <input v-model="pause" type="number" class="input-group-number rounded-input" placeholder="Minutes">
            <button v-if="pause" @click="click_03" class="btn btn-outline-secondary">Next</button>
        </div>
    </div>
</template>

<script setup>
import { ref, defineEmits } from 'vue';

const emit = defineEmits(["data"]);


const value_01 = ref(true);
const value_02 = ref(true);
const working = ref(false);
const pause = ref(false);

const click_01 = () => {
    value_01.value = false;
}

const click_02 = () => {
    value_02.value = false;
}

const click_03 = () => {
    const data = {
        'working': working.value,
        'pause': pause.value
    };
    emit('data', data);
}
</script>

<style>
    .circle {
        width: 300px;
        height: 300px;
        border-radius: 100%;
        background-color: rgb(249, 249, 249);
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        transition: background-color 0.3s, box-shadow 0.3s;
    }

    .circle.active {
        background-color: rgb(249, 249, 249);
    }

    .circle:hover {
        background-color: rgb(233, 233, 233);
        box-shadow: 2px -20px 62px 37px rgba(0,0,0,0.54);
        cursor: grab;
    }

    .input-group-number {
        border-radius: 8px;
    }

    .rounded-input {
        border-radius: 8px;
    }
</style>
